.stat {
  width: 200px;
  border: 3px solid #737373;
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}

.stat.wrongAnswer,
.stat.rightAnswer {
  font-weight: bold;
}

.stat.wrongAnswer {
  border-color: #f46868;
  color: #f46868;
}

.stat.rightAnswer {
  border-color: #3ac551;
  color: #3ac551;
}
